.mindmap {
  list-style-type: none;
  position: relative;
  width: 24em;
  height: 24em;
  padding: 5.65685425em;
  margin: 1.75em auto 0;
}
.mindmap li {
  opacity: 0.5;
  position: absolute;
  color: white;
  display: block;
  overflow: hidden;
  top: 50%;
  left: 50%;
  width: 8em;
  height: 8em;
  margin: -4em;
  border-radius: 50%;
  background: black;
  /* 12em = half the width of the wrapper */
}
.mindmap li.header {
  font-weight: bold;
}
.mindmap li.deg0 {
  transform: translate(12em);
}
.mindmap li.deg45 {
  transform: rotate(45deg) translate(12em) rotate(-45deg);
}
.mindmap li.deg135 {
  transform: rotate(135deg) translate(12em) rotate(-135deg);
}
.mindmap li.deg180 {
  transform: translate(-12em);
}
.mindmap li.deg225 {
  transform: rotate(225deg) translate(12em) rotate(-225deg);
}
.mindmap li.deg315 {
  transform: rotate(315deg) translate(12em) rotate(-315deg);
}
.mindmap li:hover {
  transition: 1s ease-out;
  opacity: 1;
}
.mindmap li > span {
  position: absolute;
  text-align: center;
  top: 50%;
  width: 100%;
}
.mindmap li:before {
  content: "";
  position: absolute;
  top: 1%;
  left: 5%;
  width: 90%;
  height: 90%;
  border-radius: 50%;
  background: radial-gradient(circle at 50% 0px, #ffffff, rgba(255, 255, 255, 0) 58%);
  filter: blur(5px);
  z-index: 2;
}
.mindmap li:after {
  content: "";
  position: absolute;
  border-radius: 100%;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: radial-gradient(circle at 50% 30%, rgba(245, 237, 48, 0), rgba(200, 190, 20, 0.2) 50%, #575300 100%);
}
#game {
  background: url('../img/game.jpg') no-repeat center;
  background-size: 100%;
}
@keyframes bubble-anim {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(0.95, 1.05);
  }
  48% {
    transform: scale(1.1, 0.9);
  }
  68% {
    transform: scale(0.98, 1.02);
  }
  80% {
    transform: scale(1.02, 0.98);
  }
  97%,
  100% {
    transform: scale(1);
  }
}
